* {
    box-sizing: border-box;
    margin: 0;
    overflow-x: hidden!important;
}

header {
    width: 100vw;
    padding: 30px 0 30px 0;
    background: #343a40;
    color: #fff;
    text-align: center;
}

header h1 {
    display: inline;
    margin-left: 15px;
    font-weight: 300;
}

input:focus, 
select:focus
{
    outline: none;
}

.input {
    width: 100px;
    padding: 20px 0 20px 0;
    margin: 0 auto;
}

.search-area{
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.search-area button {
    display: inline;
    background: #343a40;
    color: #fff;
    border: none;
    padding: 8px 13px 10px 13px;
    margin: 0 15px 0 0;
    position: relative;
    top: -2px;
}

input {
    width: 150px;
    padding: 5px;
    font-size: 1em;
}


.list {
    display: grid;
    justify-content: center;
    margin: 20px 0 50px 0;
    grid-template-columns: 25% 25% 25% 25%;
    grid-gap: 1em;
    margin: 1rem 1rem;
}

@media only screen and (max-width:1280px){
    .list {
        grid-template-columns: 33% 33% 33%;
    }
}

@media only screen and (max-width:910px){
    .list {
        grid-template-columns: 50% 50%;
    }
}

@media only screen and (max-width:590px){
    .list {
        grid-template-columns: 100%;
    }
}

.card-container {
    width: 90%;
    border: 1px solid rgba(0,0,0,.125);
    margin: 0 auto;
    border-radius: 5px;
    overflow: hidden;
}
.desc {
    height: 180px;
    padding: 10px;
}

.img-container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}

.img-container img{
    margin-top: 10px;
    height: 20px;
    width: 20px;
}

body{
    font-family: 'Open Sans Condensed', sans-serif;
}

.desc h2 {
    font-size: 1.2em;    
    font-weight: 400;
}
.desc h3, p {
    font-weight: 300;
}

.desc h3 {
    color: #6c757d;
    font-size: 1.2em;
    padding: 10px 0 10px 0;
}

.desc p {
    color: #6c757d;
}

img {
    width: 100%;
    height: 250px;
}

select {
    background-color: #343a40;
    color: #fff;
    height: 32px;
    width: 70px;
    position: relative;
    top: -2px;
}

